.store-list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    justify-items: center;
    padding-bottom: 3rem;
}


@media only screen and (max-width: 1280px) {
    .store-list {
        grid-template-columns: repeat(3, auto);
    }
}

@media only screen and (max-width: 960px) {
    .store-list {
        grid-template-columns: auto auto;
    }
}

@media only screen and (max-width: 640px) {
    .store-list {
        grid-template-columns: auto;
    }
}

.rent-page h1 {
    text-align: center;
    padding-top: 2rem;
    margin-top: 0;
}

.store {
    position: relative;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    align-items: center;
    border: solid 1px #aaa;
    border-radius: 2rem;
    width: 15rem;
    height: 8rem;
    padding: 1rem;
    background-color: #f3ecbc;
}

.store h2 {
    font-size: 1.2rem;
    margin-top: 0;
}

.store-icon {
    font-size: 3rem;
}

.store-info {
    display: grid;
    grid-template-columns: auto auto;
    align-items: flex-end;
    margin-bottom: 0.2rem;
}

.store-info span:first-child {
    width: 5rem;
}

.store-info span:nth-child(2) {
    text-align: right;
}

.store-info:last-child span:first-child {
    width: 8rem;
}

.hidden {
    display: none;
}
