.main-header {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 5rem;
    background-color: #062635;
    z-index: 1000;
}


.main-header .title {
    display: inline;
    margin: 1rem;
    height: 3rem;
    width: auto;
}

.main-menu {
    position: absolute;
    top: 0.5rem;
    left: 22rem;
    display: inline-block;
    list-style-type: none;
    margin: 0 0 0 3rem;
    padding: 0;
    bottom: 0px;
    height: 100%;
    overflow: hidden;
    vertical-align: top;
}

.main-menu li {
    float: left;
    vertical-align: middle;
    display: table;
    height: 100%;
    box-shadow: 1.53rem 0px 0px -1.5rem #fff;
    margin: 0 0.03rem 0 0.03rem; 
}

.main-menu li:first-child {
    box-shadow: 1.53rem 0px 0px -1.5rem #fff, -1.53rem 0px 0px -1.5rem #fff;
}
.main-menu li a {
    display: block;
    color: white;
    font-weight: bold;
    text-align: center;
    vertical-align: middle;
    padding: 1rem;
    height: 100%;
    display: table-cell;
    text-decoration: none;
}

.main-menu li a:hover {
    text-shadow: 0px 0px 10px #eef;
}

.hamburger-menu {
    display: none;
}

@media only screen and (max-width: 800px) {

    .main-header .title {
        height: auto;
        width: 75%;
        max-width: 20rem;
    }

    .main-menu.invisible {
        display: none;
    }

    .main-menu.visible {
        position: absolute;
        display: block;
        right: 2rem;
        z-index: 1000;
        width: 100%;
        top: 5rem;
        left: 0;
        margin: 0;
        overflow: initial;
    }

    .main-menu.visible li {
        float: initial;
        width: 100%;
        left: 0px;
        border: solid 1px #000;
        background-color: #062635;
        font-size: 1.4rem;
    }

    .hamburger-menu {
        position: absolute;
        display: inline-block;
        font-size: 2.5rem;
        color: #fff;
        right: 2rem;
        margin-top: 1rem;
        cursor: pointer;
    }

    .hamburger-menu svg {
        cursor: pointer;
    }
}
